<template>
    <div class="newsdetail">
      <div class="header">
          <div class="back"  @click.stop="back()">
              <img src="../../assets/image/联合42.png" alt="">
          </div>
      </div>
      <div class='newscontainer'>
        <div class='containerTop'>
          <div class='topTitle'>{{title}}</div>
          <div class='topWord'>
            <div class='topSource'>{{source}}</div>
            <div class='topPublishTime'>{{publishTime}}</div>
          </div>
        </div>
        <div class='news_contain'>
          <div class='video_player' >
            <div class="player">
                <video-player  class="vjs-custom-skin video-player"
                                ref="videoPlayer"
                                :options="playerOptions"
                                :playsinline="true"
                                :events="events"
                                @fullscreenchange="onPlayerFullScreenchange($event)"
                                >
                </video-player>
            </div>
          </div>
          <!--富文本编辑器-->
          <div class="editor_div1" id="editor1">

          </div>
          <div class="editor_div2" id="editor2">

          </div>
        </div>
      </div>
      <!-- 加载页面 -->
      <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>

<script>
import E from 'wangeditor'
import 'wangeditor/release/wangEditor.min.css'
import API from '@/api/api'
import $ from 'jquery'
import Loading from './../components/loading'
export default {
  components: { Loading },
  data () {
    return {
      id: '', // 新闻id
      events: ['fullscreenchange'], // 视频事件
      ifFullscreen: false, // 全屏
      verDialogVisible: true, // loading
      content: '', // 新闻内容
      title: '', // 新闻标题
      source: '', // 新闻来源
      publishTime: '', // 新闻时间
      playerOptions: {} // 视频组件
    }
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    }
  },
  created () {
    this.id = this.$route.params.id // 获取新闻id
    this.findNewsDetilById()
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  methods: {
    // 全屏按钮
    onPlayerFullScreenchange (player) {
      this.ifFullscreen = player.isFullscreen()
      // alert(this.ifFullscreen)
      // alert(1)
      // this.ifFullscreen = !this.ifFullscreen;
      // console.log(this.ifFullscreen)
    },
    // 返回键
    onBackKeyDown () {
      this.back()
    },
    // 返回上一路由
    back () {
      this.$router.back()
    },
    // 新闻积分接口
    addSelfCredits () {
      API.post('addSelfCredits', {
        creditsId: 'readingNews',
        readingId: this.id
      }, 'json', (data, err) => {
        if (data.success) {
          // console.log(data)
        }
      })
    },
    // 富文本编辑器
    fwb (content) {
      const editor1 = new E('#editor1') // 富文本编辑器
      const editor2 = new E('#editor2') // 富文本编辑器
      editor1.create()
      editor1.$textElem.attr('contenteditable', false) // 禁止编辑
      editor1.txt.html(this.content) // 富文本赋值
      editor2.create()
      editor2.$textElem.attr('contenteditable', false) // 禁止编辑
      editor2.txt.html(this.content) // 富文本赋值
    },
    // 获取新闻
    findNewsDetilById () {
      API.post('findNewsDetilById', {
        id: this.id
      }, 'json', (data, err) => {
        if (data.success) {
          // console.log(data)
          // console.log($('.newscontainer'))
          this.addSelfCredits()
          this.content = data.data.newsContent.newsBody // 获取新闻内容
          this.title = data.data.title // 获取新闻标题
          this.source = data.data.source // 获取新闻来源
          this.publishTime = this.GLOBAL.changeTime(data.data.publishTime) // 获取新闻时间
          this.fwb(this.content)
          if (data.data.newsContent.newsVideo) { // 判断是否存在视频
            $('.editor_div1').css('display', 'none')
            $('.editor_div2').css('display', 'block')
            $('.video_player').css('display', 'block')
            this.playerOptions = {
              autoplay: false,
              controls: true, // 控制条
              muted: false,
              language: 'zh-CN',
              fluid: true,
              // playbackRates: [0.7, 1.0, 1.5, 2.0],
              aspectRatio: '16:9',
              sources: [{
                type: 'video/mp4',
                src: data.data.newsContent.newsVideo
                // src:'../../assets/image/阴阳师.mp4'
              }],
              poster: data.data.videoImg,
              // poster:'http://152.136.158.153:8080/pxks/dl/3d7b28eb971f176e9a0ae2e8f9d5754c25a8413e537bff1f754c1fe31e1a6589',
              notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
              controlBar: {
                timeDivider: true,
                durationDisplay: true,
                remainingTimeDisplay: false,
                fullscreenToggle: true // 全屏按钮
              }
            }
          } else {
            $('.editor_div1').css('display', 'block')
            $('.editor_div2').css('display', 'none')
            $('.video_player').css('display', 'none')
          }
          this.verDialogVisible = false
        } else {
          console.log(data.msg)
        }
      })
    }
  },
  watch: {
    // 全屏按钮
    ifFullscreen (val, oldval) {
      if (val) {
        screen.orientation.lock('landscape')
        // eslint-disable-next-line no-undef
        StatusBar.hide()
      } else {
        screen.orientation.lock('portrait')
        // eslint-disable-next-line no-undef
        StatusBar.show()
      }
    }
  }

}
</script>
<style scoped>
    .newsdetail{
        width:100%;
        height:100vh;
    }
    .newsdetail .header{
        width:100%;
        background:#fff;
        font-size:0.36rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        display: flex;
        justify-content: center;
        z-index: 99;
    }
    .newsdetail .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .newsdetail .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .newsdetail .newscontainer{
      margin-top:0.94rem;
      width:100%;
      height:calc(100vh - 0.94rem);
    }
    .newscontainer .containerTop{
      width:100%;
      height:1.35rem;
      padding:0 0.35rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      /* border-bottom: 0.012rem solid #EBEFF5; */
    }
    .containerTop .topTitle{
      width:100%;
      font-size:0.4rem;
      color:#384150;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      line-height: 0.57rem;
      word-wrap: break-word;
      word-break: normal;
      /*! autoprefixer: off */
      /* autoprefixer: on */
    }
    .containerTop .topWord{
      width:100%;
      margin-top:0.16rem;
      margin-bottom: 0.36rem;
      display: flex;
      justify-content: space-between;
      font-size:0.26rem;
      color:#9FA7AF;
    }
    .news_contain{
      width:100%;
      padding:0.15rem 0.35rem 0 0.35rem;
      height:calc(100vh - 0.94rem - 1.35rem);
      border-top: 0.012rem solid #EBEFF5;
      /* overflow-y: scroll; */
      /* -webkit-overflow-scrolling: touch; */
      word-break: break-word
    }
    .video_player{
      width:100%;
      padding:0.3rem 0;
      height:4.5rem;
    }

</style>
<style>
    .newsdetail .w-e-toolbar{
      display: none;
    }
    .newsdetail .editor_div1 .w-e-text-container{
      border:0 !important;
      /* z-index: 100 !important; */
      height: calc(100vh - 0.94rem - 1.75rem) !important;
      /* font-size:initial; */
      /* font-weight:initial; */
    }
    .newsdetail .w-e-text div{
      min-width:100% !important;
      width:100% !important;
      margin:0px !important;
      padding:0px !important;
      float:none
    }
    .newsdetail .w-e-text p{
      width:auto !important;
      height:auto !important;
      padding:0px !important;
    }
    .newsdetail .w-e-text span{
      float:none
    }
    .newsdetail .w-e-text img{
      display: block
    }
    .newsdetail .w-e-text ul{
      width:100% !important;
    }
    .newsdetail .w-e-text ul li{
      width:100% !important;
    }
    .newsdetail .w-e-text table{
      width:100% !important;
      margin:0 !important;
    }
    .newsdetail .w-e-text td{
      margin:0 !important;
      padding:0 !important;
    }
    .newsdetail .editor_div2 .w-e-text-container{
      border:0 !important;
      /* z-index: 100 !important; */
      height: calc(100vh - 0.94rem - 1.65rem - 4.6rem) !important;
      /* font-size:initial; */
      /* font-weight:initial; */
    }
    .newsdetail .w-e-text-container b{
      font-weight:normal;
    }
    .newsdetail .w-e-text{
      border:0;
      padding:0;
      -webkit-overflow-scrolling: touch !important;
      /* font-size:initial;
      font-weight:initial; */
    }
    .newsdetail .w-e-text{
      overflow-x: hidden;
    }
    .newsdetail .w-e-text::-webkit-scrollbar {display:none}
    .newsdetail .w-e-text img:hover {
        box-shadow: none !important;
    }
</style>



